.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

img{
  z-index: -1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

#Main{
  padding: 10px;
  color: white;
  border-radius: 10px;
  background-color: rgba(48, 48, 48, 0.788);
}
  #form{
    margin: 10px;
  }
    #form div{
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      white-space: nowrap;
      gap: 10px;
      max-height: 20px;
    }